.containerDialog{
    display: flex;
    width: 1200px;
}
.listDialogs{
    display: flex;
    flex-direction: column;
    width: 255px;
    border: 1px solid #666;
    min-height: 110px;
    max-height: 691px;
    overflow-y: auto;
}
/*dialog*/
.dialogUser img {
    width: 34px;
    border-radius: 34px;
    height: 34px;
}
.dialogUser{
    display: inherit;
    border-bottom: 1px solid black;
}
.userNameWithImg{
    padding: 5px;
    transition: 0.3s all ease;
}
.userNameWithImg h3{
    display: initial;
    font-size: 14px;
    font-weight: 600;
    left: 10px;
    position: relative;
    bottom: 10px;
}
.userNameWithImg:hover{
    background: black;
    color: white;
}
.dialogUser a{
    color: black;
    text-decoration: none;
    text-decoration-color: black;
    width: 100%;
}
/*dialog*/
.messagesContain{
    width: 100%;
    border: 1px solid;
}
.NotViews{
    background-color: red !important;
}
.messagesWithFriendId,.dialog,.notViewedMessage{
    padding: 10px;
    margin-bottom: 10px;
    background: black;
    color: white;
    border-radius: 27px;
}
.notViewedMessage{
    background-color: #4e4a4a !important;
}


.main__img__username{
    display: flex;
}
.main__img__username h4{
    margin: 0;
    padding: 0;
    margin-left: 10px;
}
.chatText{
    margin-left: 43px;
}
.mainChatBlock {
    width: 100%;
    display: inline-block;
    background-color: #eee;
}
.chatTittle{
    border: 1px solid black;
    padding: 10px;
    width: auto;
    display: flow-root;
    background: black;
    color: white;
}
.chatTittle img{
    width: 34px;
    border-radius: 30px;
    height: 34px;
    float: right;
}
.chatTittle h3{
    display: inline;
    font-size: 14px;
    font-weight: 500;
}
.chatBody {
    width: auto;
    background-color: #fff;
    border: 1px solid #666;
    min-height: 110px;
    max-height: 550px;
    overflow-y: auto;
    padding: 10px;
}
.chatBody img{
    width: 34px;
    border-radius: 34px;
}
.chatBody::-webkit-scrollbar { width: 8px; height: 3px;}
.chatBody::-webkit-scrollbar-button {  background-color: #666; }
.chatBody::-webkit-scrollbar-track {  background-color: #999;}
.chatBody::-webkit-scrollbar-track-piece { background-color: #ffffff;}
.chatBody::-webkit-scrollbar-thumb { height: 50px; background-color: #666; border-radius: 3px;}
.chatBody::-webkit-scrollbar-corner { background-color: #999;}
.chatBody::-webkit-resizer { background-color: #666;}

.past__messages{
    margin: 10px auto;
    display: block;
    border: 0px;
    background-color: #999b9cc4;
    color: white;
    padding: 5px;
    border-radius: 10px;
    cursor: pointer;
    transition: 0.3s all ease;
    outline: none;
}
.past__messages:hover{
    background-color: #999b9c;
}
@media screen and (max-width:800px) {
    .containerDialog{
        display: block;
        width: 100%;
    }
    .listDialogs{
        width: auto;
        display: block;
        min-height: 110px;
    }
    .userNameWithImg{
        padding: 15px;
    }
}